export const sourceCode = "---\r\ntitle: \"ভূমিকম্প পরিমাপের যন্ত্র\"\r\ndescription: \"ভূমিকম্প পরিমাপের যন্ত্র সম্পর্কে বিস্তারিত তথ্য এবং এটি কীভাবে কাজ করে তা নিয়ে আলোচনা।\"\r\n---\r\n\r\n## ভূমিকম্প পরিমাপের যন্ত্র কী?\r\n\r\nভূমিকম্প পরিমাপের যন্ত্রকে সিসমোমিটার (Seismometer) বা সিসমোগ্রাফ (Seismograph) বলা হয়। এটি একটি বৈজ্ঞানিক যন্ত্র যা ভূমিকম্পের সময় মাটির কম্পন বা দোলন পরিমাপ করে। সিসমোমিটার মাটির নড়াচড়া শনাক্ত করে এবং সেই তথ্যকে রেকর্ড করে, যা ভূমিকম্পের তীব্রতা, স্থান এবং সময় নির্ধারণে সাহায্য করে।";
