export const sourceCode = "---\r\ntitle: \"ষাট গম্বুজ মসজিদ কোথায় অবস্থিত?\"\r\ndescription: \"ষাট গম্বুজ মসজিদ সম্পর্কে বিস্তারিত তথ্য এবং এটি কোথায় অবস্থিত তা নিয়ে আলোচনা।\"\r\ntags: [\"General Knowledge\", \"Mosque\", \"Sixty Domes Mosque\"]\r\n---\r\n\r\n## ষাট গম্বুজ মসজিদ কোথায় অবস্থিত? কতটি গম্বুজ রয়েছে?\r\n\r\nষাট গম্বুজ মসজিদ বাংলাদেশের বাগেরহাট জেলায় অবস্থিত একটি ঐতিহাসিক মসজিদ। এটি ১৪৫৯ সালে নির্মিত হয় এবং এর নামকরণ করা হয়েছে এর বিশিষ্ট বৈশিষ্ট্যের কারণে, যেখানে মোট **৮১টি গম্বুজ** রয়েছে। এই মসজিদটি বাগেরহাটের অন্যতম প্রধান পর্যটন আকর্ষণ এবং এটি ইউনেস্কো বিশ্ব ঐতিহ্য স্থল হিসেবে স্বীকৃত।";
