export const sourceCode = "---\r\ntitle: \"আন্দিজ পর্বতমালা কোথায় অবস্থিত?\"\r\ndescription: \"আন্দিজ পর্বতমালা সম্পর্কে বিস্তারিত তথ্য এবং এটি কোথায় অবস্থিত তা নিয়ে আলোচনা।\"\r\n---\r\n\r\n## আন্দিজ পর্বতমাল কোথায় অবস্থিত?\r\n\r\nআন্দিজ পর্বতমালা বিশ্বের দীর্ঘতম স্থলভাগের পর্বতমালা। এটি দক্ষিণ আমেরিকার পশ্চিম প্রান্ত বরাবর প্রায় **৭,০০০ কিলোমিটার** বিস্তৃত। প্রাকৃতিক সৌন্দর্য, প্রত্নতাত্ত্বিক নিদর্শন এবং বৈচিত্র্যময় জলবায়ুর জন্য আন্দিজ বিশ্বের অন্যতম জনপ্রিয় পর্বতমালা হিসেবে পরিচিত।\r\n\r\n---\r\n\r\n## আন্দিজ পর্বতমালা কোন কোন দেশে অবস্থিত?\r\n\r\nআন্দিজ পর্বতমালা মোট **৭টি দেশ** জুড়ে বিস্তৃত। দেশগুলো হলো—\r\n\r\n- **ভেনেজুয়েলা**\r\n- **কলম্বিয়া**\r\n- **ইকুয়েডর**\r\n- **পেরু**\r\n- **বলিভিয়া**\r\n- **চিলি**\r\n- **আর্জেন্টিনা**\r\n\r\nপর্বতমালাটি উত্তর থেকে দক্ষিণ দিকে বিস্তৃত হয়ে দক্ষিণ আমেরিকা মহাদেশের একটি প্রাকৃতিক মেরুদণ্ড তৈরি করেছে।\r\n\r\n---\r\n\r\n## আন্দিজ পর্বতমালার দৈর্ঘ্য ও উচ্চতা\r\n\r\n- মোট দৈর্ঘ্য: **প্রায় ৭,০০০ কিলোমিটার**\r\n- সর্বোচ্চ শৃঙ্গ: **মাউন্ট আকেরংকাগুয়া (Mount Aconcagua)**\r\n- অবস্থান: আর্জেন্টিনা\r\n- উচ্চতা: **৬,৯৬১ মিটার** (২২,৮৩৭ ফুট)\r\n\r\nএটি **হিমালয় ব্যতীত বিশ্বের সর্বোচ্চ পর্বত**।\r\n\r\n---\r\n\r\n## আন্দিজ পর্বতমালার বৈশিষ্ট্য\r\n\r\nআন্দিজ পর্বতমালা এত জনপ্রিয় হওয়ার মূল কারণ হলো এর অসাধারণ বৈচিত্র্য:\r\n\r\n### ১. বৈচিত্র্যময় জলবায়ু\r\n\r\nআন্দিজ পর্বতমালা জুড়ে রয়েছে—\r\n\r\n- তুষারঢাকা শৃঙ্গ\r\n- সবুজ উপত্যকা\r\n- ট্রপিকাল বন\r\n- শুকনো মরুভূমি (যেমন: আটাকামা মরুভূমি)\r\n\r\n### ২. সমৃদ্ধ বন্যপ্রাণী\r\n\r\nএখানে পাওয়া যায়—\r\n\r\n- লামা\r\n- আলপাকা\r\n- কন্ডর\r\n- ভিকুনা\r\n- নানা প্রজাতির অর্কিড ও অনন্য উদ্ভিদ\r\n\r\n### ৩. প্রাচীন ইনকা সভ্যতার কেন্দ্র\r\n\r\nআন্দিজ ছিল ইনকাদের মূল বসতি। এখনো দেখা যায়—\r\n\r\n- **মাচু পিচু**\r\n- স্যাক্রেড ভ্যালি\r\n- প্রাচীন ইনকা পথ\r\n\r\n---\r\n\r\n## আন্দিজ পর্বতমালা কেন বিখ্যাত?\r\n\r\n- **বিশ্বের দীর্ঘতম পর্বতমালা** হওয়ার কারণে\r\n- বিভিন্ন দেশ ও সংস্কৃতির মিলনস্থল\r\n- তুলনাহীন প্রাকৃতিক সৌন্দর্য\r\n- অ্যাডভেঞ্চার পর্যটনের স্বর্গ — ট্রেকিং, হাইকিং, রক ক্লাইম্বিং\r\n- ইনকা সভ্যতা ও ঐতিহ্যের কেন্দ্রবিন্দু\r\n\r\n---\r\n\r\n## পর্যটকদের জন্য প্রধান আকর্ষণ\r\n\r\n- **মাচু পিচু, পেরু**\r\n- **আটাকামা মরুভূমি, চিলি**\r\n- **লেক টিটিকাকা, পেরু–বলিভিয়া**\r\n- **পাটাগোনিয়া অঞ্চল, চিলি ও আর্জেন্টিনা**\r\n- **কোলকা ক্যানিয়ন, পেরু**\r\n\r\n---\r\n\r\n## উপসংহার\r\n\r\nআন্দিজ পর্বতমালা শুধুমাত্র একটি পর্বতমালা নয়—এটি ইতিহাস, প্রকৃতি, সংস্কৃতি এবং অভিযাত্রার এক অনন্য সমন্বয়। দক্ষিণ আমেরিকার সাতটি দেশ জুড়ে বিস্তৃত এই পর্বতমালা প্রকৃতিপ্রেমী ও অ্যাডভেঞ্চারপ্রেমীদের জন্য স্বপ্নের গন্তব্য।";
