export const sourceCode = "---\r\ntitle: \"জাতিসংঘের নিরাপত্তা পরিষদের সদস্যরা কারা?\"\r\ndescription: \"জাতিসংঘের নিরাপত্তা পরিষদের সদস্যদের সম্পর্কে বিস্তারিত তথ্য এবং তারা কারা তা নিয়ে আলোচনা।\"\r\n---\r\n\r\n## জাতিসংঘের নিরাপত্তা পরিষদের সদস্যরা কারা?\r\n\r\nজাতিসংঘের নিরাপত্তা পরিষদ (United Nations Security Council - UNSC) হলো জাতিসংঘের একটি প্রধান সংস্থা যা আন্তর্জাতিক শান্তি ও নিরাপত্তা রক্ষা করার জন্য দায়িত্বপ্রাপ্ত। নিরাপত্তা পরিষদে মোট **১৫টি সদস্য দেশ** রয়েছে, যার মধ্যে **৫টি স্থায়ী সদস্য** এবং **১০টি অস্থায়ী সদস্য** রয়েছে।\r\n\r\n### স্থায়ী সদস্যরা:\r\n\r\n১. **চীন**\r\n২. **ফ্রান্স**\r\n৩. **রাশিয়া**\r\n৪. **যুক্তরাজ্য**\r\n৫. **যুক্তরাষ্ট্র**\r\n\r\n### অস্থায়ী সদস্যরা:\r\n\r\nঅস্থায়ী সদস্যরা প্রতি দুই বছর অন্তর নির্বাচিত হয় এবং তারা নিরাপত্তা পরিষদের কার্যক্রমে অংশগ্রহণ করে। বর্তমানে অস্থায়ী সদস্যদের তালিকা পরিবর্তিত হতে পারে, তাই সর্বশেষ তথ্যের জন্য জাতিসংঘের অফিসিয়াল ওয়েবসাইট দেখতে পারেন।";
